<template>
  <el-container style="margin: 50px">
    <div>
      <img
        :src="avatar?avatar:'https://dss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1155772843,599594265&fm=26&gp=0.jpg'"
        style="width: 300px;height:260px;display:block;margin-bottom:20px"
      />
      <input type="file" class="form-control-file" @change="avatarChange" />
    </div>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px">
      <el-form-item label="" prop="username">
        <el-input type="text" v-model="ruleForm.username" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="gender">
        <el-select v-model="ruleForm.gender">
          <el-option label="" value="male"></el-option>
          <el-option label="" value="female"></el-option>
          <el-option label="" value="baomi"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="age">
        <el-input v-model.number="ruleForm.age"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="success" @click="submitForm"></el-button>
      </el-form-item>
    </el-form>
  </el-container>
</template>
<script>
export default {
  data() {
    var checkAge = (rule, value, callback) => {
      if (value < 18) {
        // 
        return callback(new Error("18"));
      } else {
        // 
        callback();
      }
    };
    return {
      managerid: "",
      ruleForm: {
        username: "",
        password: "",
        gender: "",
        age: "",
      },
      imageUrl: "",
      avatar:"",
      _id: "",
      rules: {
        age: [
          { type: "number", message: "", trigger: "change" },
          // 
          {
            validator: checkAge,
            trigger: "change",
          },
        ],
        password: [
          {
            min: 3,
            max: 12,
            message: " 3  12 ",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    submitForm() {
      this.$refs["ruleForm"].validate(async (valid) => {
        // validtrue,false
        if (valid) {
          let currentUser = localStorage.getItem("currentUser");
          currentUser = JSON.parse(currentUser);
          let _id = currentUser.data[0]._id;
          this._id = _id;
          const { ruleForm } = this;
          const { data } = await this.$request.put("/manager/" + _id, {
            ...ruleForm,
          });
          if (data.code === 1) {
            this.$message({
              type: "success",
              message: "",              
            });
            
            this.$router.push("/login")
          }
        } else {
          return false;
        }
      });
    },
    async avatarChange(e) {
      let currentUser = localStorage.getItem("currentUser");
      currentUser = JSON.parse(currentUser);
      const id = currentUser.data[0]._id;
      const data = new FormData();
      data.set("_id", id);
      data.set("avatar", e.target.files[0]);
     
      const result = await this.$request.post("/uploads/manager", data, {
        contentType: false,
      }); 

      this.avatar = result.data.data.avatarUrl;
      // 
      if (id === currentUser._id) {
        currentUser.avatarUrl = result.data.avatarUrl;
        localStorage.setItem("currentUser", JSON.stringify(currentUser));
      }

    },
  },
  async created() {
    let currentUser = localStorage.getItem("currentUser");
    currentUser = JSON.parse(currentUser);
    this.ruleForm.username = currentUser.data[0].username;
    this.ruleForm.age = currentUser.data[0].age;
    this.ruleForm.gender = currentUser.data[0].gender;
    this.avatar = currentUser.data[0].avatarUrl;
  },
};
</script>
<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>