<template>
  <div>
    <h1></h1>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px">
      <el-form-item label="" prop="username">
        <el-input type="text" v-bind:value="ruleForm.username" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="gender">
        <el-select v-model="ruleForm.gender">
          <el-option label="" value="male"></el-option>
          <el-option label="" value="female"></el-option>
          <el-option label="" value="baomi"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="age">
        <el-input v-model.number="ruleForm.age"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="success" @click="submitForm"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    var checkAge = (rule, value, callback) => {
      if (value < 18) {
        // 
        return callback(new Error("18"));
      } else {
        // 
        callback();
      }
    };
    return {
      userid: "",
      ruleForm: {
        username: "",
        password: "",
        gender: "male",
        age: "",
      },
      rules: {
        age: [
          { required: true, message: "", trigger: "change" },
          { type: "number", message: "", trigger: "change" },
          // 
          {
            validator: checkAge,
            trigger: "change",
          },
        ],
        password: [
          {
            min: 6,
            max: 12,
            message: " 6  12 ",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    submitForm() {
      this.$refs["ruleForm"].validate(async (valid) => {
        // validtrue,false
        if (valid) {
          const { userid, ruleForm } = this;
          const { data } = await this.$request.put("/manager/" + userid, {
            ...ruleForm,
          });
          if (data.code === 1) {
            this.$message({
              type: "success",
              message: "",
            });
          }
        } else {
          return false;
        }
      });
    },
  },
  async created() {
    const { a, b } = this.$route.query;
    const { id } = this.$route.params;
    const { data } = await this.$request.get("/manager/" + id);
    this.userid = id;
    Object.assign(this.ruleForm, data.data);
  },
};
</script>